import React from 'react'
import CircularProgress from '@material-ui/core/CircularProgress'

// Style
import classes from './LoadingIndicator.module.scss'

export default function LoadingIndicator (props) {
  // Variables
  const { loading = false, children } = props
  // Services
  // Events
  // Callbacks
  // Local Variables

  return (
    <React.Fragment>
      {loading ? (
        <div className={classes.loading} id='loading-overlay'>
          <CircularProgress id='fetching-animation' className={classes.animation} size={50} />
        </div>
      ) : (
        children
      )}
    </React.Fragment>
  )
}
