import React from 'react'
import { Switch, Route } from 'react-router-dom'

// Pages of Payment Request Officer
import PaymentRequestPage from '../Components/PaymentRequest/PaymentRequestPage/PaymentRequestPage'
import ClaimsUtilLedgerReport from './../Components/ClaimsUtilLedgerReport/ClaimsUtilLedgerReport'

// Pages of Payment Request Officer Supervisor
import Dashboard from './../Components/PaymentRequestSupervisor/Dashboard/Dashboard'
import SubmittedPaymentRequestPage from './../Components/PaymentRequest/SubmittedPaymentRequestPage/SubmittedPaymentRequestPage'
import SubmittedPaymentRequestList from './../Components/PaymentRequestSupervisor/SubmittedPaymentRequestList/SubmittedPaymentRequestList'
import LoadingHimsData from './../Components/LoadingHimsData/LoadingHimsData'

// Services
import { SideBarService } from 'SharedComponents/SideBarComponent/SideBarComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

const sideBarItems = [
  {
    label: 'Dashboard',
    icon: 'home',
    to: '/'
  },
  {
    label: 'Claims',
    icon: 'file-alt',
    to: '/claims'
  },
  {
    label: 'Reports',
    icon: 'chart-bar',
    type: 'popover',
    child: [
      { label: 'Generate Utilization Ledger', to: '/utilization-ledger' },
      { label: 'Template for Loading HIMS Data', to: '/loading-hims-data' }
    ]
  }
]

const breadcrumbs = {
  dashboard: {
    text: 'Dashboard',
    to: '/'
  },
  paymentRequest: {
    text: 'Payment Request',
    to: '/payment-request'
  },
  specificPaymentRequest: {
    text: data => data,
    to: data => `/payment-request/${data}`
  },
  submittedPaymentRequest: {
    text: 'Submitted Payment Request',
    to: `/payment-requests/list`
  },
  reports: {
    text: 'Template for Loading HIMS Data'
  },
  'utilization-ledger': {
    text: 'Utilization Ledger'
  }
}

export default () => {
  SideBarService.setItems(sideBarItems)
  BreadcrumbsService.setItems(breadcrumbs)

  return (
    <Switch>
      <Route path='/' exact component={Dashboard} />
      <Route path='/utilization-ledger' exact render={() => <ClaimsUtilLedgerReport />} />
      <Route path='/payment-request/:bundle_number' exact component={PaymentRequestPage} />
      <Route
        path='/payment-request/:bundle_number/submitted'
        exact
        render={props => <SubmittedPaymentRequestPage supervisor {...props} />}
      />
      <Route path='/payment-requests/:pr_officer/list' exact component={SubmittedPaymentRequestList} />
      <Route path='/loading-hims-data' exact render={props => <LoadingHimsData supervisor {...props} />} />
    </Switch>
  )
}
