const WrappedTextField = props => <ClaimsInputBase {...props} />

const ClaimsInputBase = props => {
  const { error, ...others } = props
  return (
    <React.Fragment>
      <Paper className={`${classes['text-field']}   ${error ? classes['has-error'] : ''} box-shadow-field `}>
        <InputBase
          // id={id}
          // inputProps={{ ref: InputRef }}
          format='MM/DD/YYYY'
          className={classes.input}
          {...others}
          // value={p.value !== p.masking && !isNaN(p.masking) ? p.masking : p.value}
          // onChange={event => handleDateChange(moment(event.target.value))}
          // onFocus={handleOnFocus}
        />

        {/* <div className={classes['date-button']}>
          <IconButton
            // id={`${id}-date-button`}
            className={classes.button}
            // disabled={disableComponents}
            size='small'
            // onClick={() => setOpen(true)}
          >
            <FontAwesomeIcon icon='calendar-day' className={classes.icon} />
          </IconButton>
        </div> */}
      </Paper>
      {error && <span className={classes.error}>{error}</span>}
    </React.Fragment>
  )
}

export default function Test (props) {
  const [storage, setStorage] = React.useState({
    facility_id: '',
    facility_name: '',
    date_from: null
  })

  const [errors, setErrors] = React.useState({ date_from: '' })

  const [suggestion, setSuggestion] = React.useState({
    facilities: []
  })

  const [loading, setLoading] = React.useState(false)

  const [debounce, setDebounce] = React.useState({
    facility: null,
    doctor: null,
    icd10: null,
    cpt: null,
    hospital_items: null,
    pro: null
  })

  React.useEffect(() => {
    PageTitleService('Test')
    BreadcrumbsService.setSelectedItems(['test'])
  }, [])

  const getFacilitySuggestions = ({ value = '', by }) => {
    // if (value.trim().length < 3) return

    clearTimeout(debounce.facility)
    setDebounce(state => ({
      ...state,
      facility: setTimeout(() => {
        if (value) {
          setLoading(true)
          ClaimsAdjudicationServices.lookups({
            on: 'medical-partner',
            params: { query: value, by: by.split('_')[1] }
          }).then(res => {
            setLoading(false)
            const { data } = res.data
            setSuggestion(state => ({ ...state, facilities: data }))
          })
        }
      }, 500)
    }))
  }

  const onChange = ({ value, field }) => {
    console.log('onChaneg', value)
    setStorage(state => ({ ...state, [field]: value }))
  }

  const onChangeFacility = ({ id = '', name = '' }) => {
    setStorage(state => ({ ...state, facility_id: id, facility_name: name }))
  }

  const dateValidate = () => {
    const { error } = DateValidator({ date_from: storage.date_from })
    setErrors(error || {})
  }

  return (
    <div className={classes.root}>
      <div className={classes.row}>
        <div className={classes.input}>
          <div className={classes['proto-auto-complete']}>
            <div className={classes.root}>
              <Typography variant='subtitle2' className={classes.label}>
                {'Sample Documentation Using TextField'}
              </Typography>
              <div className={classes.wrapper}>
                <Paper>
                  <Autocomplete
                    id='combo-box-demo'
                    options={top100Films}
                    getOptionLabel={option => option.title}
                    style={{ width: 300 }}
                    renderInput={params => <TextField {...params} label='Combo box' variant='outlined' />}
                  />
                </Paper>
              </div>
            </div>
          </div>
        </div>
        <div className={classes.input}>
          <div className={classes['proto-auto-complete']}>
            <div className={classes.root}>
              <Typography variant='subtitle2' className={classes.label}>
                {'Medical Partner ID using TextField'}
              </Typography>

              <div className={classes.wrapper}>
                <Paper>
                  <Autocomplete
                    style={{ width: 300 }}
                    options={suggestion.facilities}
                    getOptionLabel={option => option.id}
                    freeSolo
                    loading={loading}
                    renderInput={params => <TextField {...params} label='Combo box' variant='outlined' />}
                    onClose={() => setSuggestion(state => ({ ...state, facilities: [] }))}
                    onChange={(event, value) => onChangeFacility(value || [])}
                    onInputChange={(event, value = '') => {
                      getFacilitySuggestions({ value, by: 'facility_id' })
                      onChange({ value, field: 'facility_id' })
                    }}
                    // value={storage.facility_id}
                    inputValue={storage.facility_id}
                  />
                </Paper>
              </div>
            </div>
          </div>
        </div>
        <div className={classes.input}>
          <div className={classes['proto-auto-complete']}>
            <div className={classes.root}>
              <Typography variant='subtitle2' className={classes.label}>
                {'Medical Partner (Facility) Name TextField'}
              </Typography>

              <div className={classes.wrapper}>
                <Paper>
                  <Autocomplete
                    style={{ width: 300 }}
                    options={suggestion.facilities}
                    getOptionLabel={option => option.name}
                    freeSolo
                    loading={loading}
                    renderInput={params => <TextField {...params} label='Combo box' variant='outlined' />}
                    onClose={() => setSuggestion(state => ({ ...state, facilities: [] }))}
                    onChange={(event, value) => onChangeFacility(value || [])}
                    onInputChange={(event, value = '') => {
                      getFacilitySuggestions({ value, by: 'facility_name' })
                      onChange({ value, field: 'facility_name' })
                    }}
                    // value={storage.facility_name}
                    inputValue={storage.facility_name}
                  />
                </Paper>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className={classes.row}>
        <div className={classes.input}>
          <div className={classes['proto-auto-complete']}>
            <div className={classes.root}>
              <Typography variant='subtitle2' className={classes.label}>
                {'Medical Partner ID using InputBase'}
              </Typography>

              <div className={classes.wrapper}>
                <Paper>
                  <Autocomplete
                    style={{ width: 300 }}
                    options={suggestion.facilities}
                    getOptionLabel={option => option.id}
                    freeSolo
                    loading={loading}
                    renderInput={params => (
                      <InputBase {...params} ref={params.InputProps.ref} className={classes.input} />
                    )}
                    onClose={() => setSuggestion(state => ({ ...state, facilities: [] }))}
                    onChange={(event, value) => onChangeFacility(value || [])}
                    onInputChange={(event, value = '') => {
                      getFacilitySuggestions({ value, by: 'facility_id' })
                      onChange({ value, field: 'facility_id' })
                    }}
                    // value={storage.facility_id}
                    inputValue={storage.facility_id}
                  />
                </Paper>
              </div>
            </div>
          </div>
        </div>
        <div className={classes.input}>
          <div className={classes['proto-auto-complete']}>
            <div className={classes.root}>
              <Typography variant='subtitle2' className={classes.label}>
                {'Medical Partner (Facility) Name InputBase'}
              </Typography>

              <div className={classes.wrapper}>
                <Paper>
                  <Autocomplete
                    style={{ width: 300 }}
                    options={suggestion.facilities}
                    getOptionLabel={option => option.name}
                    freeSolo
                    loading={loading}
                    renderInput={params => (
                      <InputBase {...params} ref={params.InputProps.ref} className={classes.input} />
                    )}
                    onClose={() => setSuggestion(state => ({ ...state, facilities: [] }))}
                    onChange={(event, value) => onChangeFacility(value || [])}
                    onInputChange={(event, value = '') => {
                      getFacilitySuggestions({ value, by: 'facility_name' })
                      onChange({ value, field: 'facility_name' })
                    }}
                    // value={storage.facility_name}
                    inputValue={storage.facility_name}
                  />
                </Paper>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className={classes.row}>
        <div className={classes.input}>
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <KeyboardDatePicker
              label={'Stock'}
              value={storage.date_from}
              onChange={date => onChange({ value: date, field: 'date_from' })}
              InputProps={{
                endAdornment: (
                  <IconButton onClick={() => onChange({ value: null, field: 'date_from' })}>
                    <ClearIcon />
                  </IconButton>
                )
              }}
              InputAdornmentProps={{
                position: 'start'
              }}
              format='MM/DD/YYYY'
              clearable
            />
          </MuiPickersUtilsProvider>
        </div>

        <div className={classes.input}>
          'Stock + InputBase
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <KeyboardDatePicker
              label={'Stock + InputBase'}
              value={storage.date_from}
              onChange={date => onChange({ value: date, field: 'date_from' })}
              TextFieldComponent={ClaimsInputBase}
              // InputProps={{
              //   inputComponent: ClaimsInputBase
              // }}
              format='MM/DD/YYYY'
              allowKeyboardControl
              clearable
              showTodayButton
            />
          </MuiPickersUtilsProvider>
        </div>

        <div className={classes.input}>
          <DatePickerCurrent
            label='Current'
            value={storage.date_from}
            onChange={date => {
              onChange({ value: new Date(date), field: 'date_from' })
            }}
            error={errors.date_from}
          />
        </div>
        <div className={classes.input}>
          <DatePicker
            label='New'
            value={storage.date_from}
            onChange={date => {
              onChange({ value: date, field: 'date_from' })
            }}
            error={errors.date_from}
          />
        </div>

        <div className={classes.input}>
          <Button
            variant='contained'
            color='primary'
            className={`${classes.save} button-positive`}
            onClick={dateValidate}
            id='save'
          >
            Validate
          </Button>
        </div>
      </div>
    </div>
  )
}
