import 'date-fns'
import React from 'react'
import MomentUtils from '@date-io/moment'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers'
import { DateFormatter } from 'Utilities/TextFormatter'

// Libraries
import moment from 'moment'

import classes from './DatePicker.module.scss'

/**
 * Props
 * id
 * label
 * required
 * value - should be date type
 * format
 * onChange
 *
 * other props
 * https://material-ui-pickers.dev/api/KeyboardDatePicker
 */

const ClaimsInputBase = props => {
  const {
    error,
    value,
    InputRef,
    setOpen,
    handleOnFocus,
    handleDateChange,
    noMargin,
    id,
    handleOnBlur,
    focus,
    disableComponents,
    ...other
  } = props
  return (
    <React.Fragment>
      <Paper
        className={`${classes['text-field']} ${noMargin ? classes['no-margin'] : ''}  ${
          error ? classes['has-error'] : focus && classes.focus
        } box-shadow-field ${disableComponents && classes.disabled}`}
        onBlur={handleOnBlur}
      >
        <InputBase
          id={id}
          inputProps={{ ref: InputRef }}
          className={classes.input}
          format='MM/DD/YYYY'
          value={value}
          onChange={event => handleDateChange(moment(event.target.value))}
          onFocus={handleOnFocus}
          disabled={disableComponents}
          {...other}
        />

        <div className={classes['date-button']}>
          <IconButton
            id={`${id}-date-button`}
            className={classes.button}
            disabled={disableComponents}
            size='small'
            onClick={() => setOpen(true)}
          >
            <FontAwesomeIcon icon='calendar-day' className={classes.icon} />
          </IconButton>
        </div>
      </Paper>
      {error && <span className={classes.error}>{error}</span>}
    </React.Fragment>
  )
}

export default function DatePicker (props) {
  const { id, label, required, value, format, readOnly, onChange, error, tabindex, ...other } = props
  const [open, setOpen] = React.useState(false)
  // const [selectedDate, setSelectedDate] = React.useState()

  const InputRef = React.useRef()
  const [focus, setFocus] = React.useState()

  // React.useEffect(() => {
  //   setSelectedDate(new Date(value))
  // }, [value])

  const handleOnClose = () => {
    setOpen(false)
    handleOnFocus()
  }
  const handleDateChange = date => {
    setOpen(false)
    // setSelectedDate(date)
    if (typeof onChange === 'function') onChange(date)

    handleOnFocus()
  }

  const handleOnFocus = () => {
    setFocus(true)
    setTimeout(() => InputRef.current.focus(), 1)
  }

  const handleOnBlur = e => {
    if (e.currentTarget.contains(e.relatedTarget)) return
    setFocus(false)
  }

  return (
    <div className={classes.root}>
      {label && (
        <Typography variant='subtitle2' className={classes.label}>
          {label}
          {!readOnly && required ? ' *' : ''}
        </Typography>
      )}
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography
            readOnly
            id={props.id}
            variant='subtitle2'
            className={`${classes.label} ${classes['read-only-value']}`}
          >
            {DateFormatter(props.value)}
          </Typography>
        ) : (
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <KeyboardDatePicker
              TextFieldComponent={p =>
                ClaimsInputBase({
                  ...p,
                  focus,
                  setOpen,
                  handleDateChange,
                  InputRef,
                  handleOnFocus,
                  id,
                  handleOnBlur,
                  ...other
                })
              }
              value={value}
              open={open}
              onClose={handleOnClose}
              onChange={handleDateChange}
              allowKeyboardControl
              format='MM/DD/YYYY'
              clearable
              showTodayButton
              {...other}
            />
          </MuiPickersUtilsProvider>
        )}
      </div>
    </div>
  )
}
